.AboutMe {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem;
  font-family: "Roboto";
}

.text-container {
  opacity: 0.9;
  font-size: 2.5rem;
  text-transform: uppercase;
  text-shadow: 0 0 0.1rem white;

}

.text-container h1 {
  font-family: 'Migha';
  font-size: 6rem;
  opacity: 0.7;
  text-shadow: 0 0 0.1rem var(--main-yellow), 0 0 0.3rem white;

}

.image-container {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  margin-right: 3rem;
  border: 2px solid var(--main-yellow);
  box-shadow: 0 0 0.2rem white, 0 0 0.5rem var(--main-yellow);
}

.image-container img {
  width: auto;
  max-width: 300px;
  height: auto;
}

@media (max-width: 600px) {
  .AboutMe {
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    text-align: center;
  }

  .text-container {
    font-size: 1rem;
    text-align: center;
  }

  .text-container h1 {
    font-size: 2rem;
  }

  .image-container {
    margin-right: 0;
    margin-top: 1rem;
    display:none;
  }

  .image-container img {
    max-width: 100px;
  }
}

@media (min-width: 601px) and (max-width: 900px) {
  .text-container {
    font-size: 1.8rem;
  }

  .text-container h1 {
    font-size: 3.5rem;
  }

  .image-container img {
    max-width: 220px;
  }
}

@media (min-width: 901px) and (max-width: 1200px) {
  .text-container {
    font-size: 2.2rem;
  }

  .text-container h1 {
    font-size: 5rem;
  }

  .image-container img {
    max-width: 250px;
  }
}

@media (min-width: 1201px) {
  .text-container {
    font-size: 2.5rem;
  }

  .text-container h1 {
    font-size: 6rem;
  }

  .image-container img {
    max-width: 300px;
  }
}
